// FLOWER
export const MAT_Flower = new THREE.MeshStandardMaterial({
  name: 'Flower',
  color: 'hsl(323, 66%, 67%)',
  roughness: 0.5,
  metalness: 0,
  side: THREE.DoubleSide,
});

// PINK BUILDING
export const MAT_Building = new THREE.MeshStandardMaterial({
  name: 'Building',
  color: 'hsl(323, 66%, 67%)',
  roughness: 0.75,
  metalness: 0,
});

// NEON WHITE
export const MAT_NeonWhite = new THREE.MeshStandardMaterial({
  name: 'Neon',
  color: 0xffffff,
  emissive: 0xffffff,
  emissiveIntensity: 2,
  roughness: 0,
  metalness: 0,
});

// NEON PINK
export const MAT_NeonPink = new THREE.MeshStandardMaterial({
  name: 'Neon',
  color: 'hsl(323, 66%, 67%)',
  emissive: 'hsl(323, 66%, 67%)',
  emissiveIntensity: 2,
  roughness: 0,
  metalness: 0,
});

// STAIR PILLAR
export const MAT_StairPillar = new THREE.MeshPhysicalMaterial({
  name: 'StairPillar',
  color: 'hsl(0,0%,12%)',
  roughness: 0.13,
  metalness: 1,
  transparency: 0.33,
  transparent: true,
  depthWrite: false,
  envMap: HdriPink,
});
