import * as THREE from 'three';
import State from 'controls-state';
import WebGLApp from './lib/WebGLApp';
import assets from './lib/AssetManager';
import { addEnvironment } from './scene/lights';
import Ocean from './scene/Ocean';
import Gallery from './scene/Meshes/Gallery';
import { GUI } from 'three/examples/jsm/libs/dat.gui.module';

// Post-Processing
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass';
// import passVert from './scene/shaders/pass.vert';
// import vignetteFrag from './scene/shaders/vignette.frag';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass';
import { SAOPass } from 'three/examples/jsm/postprocessing/SAOPass';
//

//
// SETUP
//

// window.DEBUG = window.location.search.includes('debug');
window.DEBUG = true;

const canvas = document.querySelector('#app');

// setup the WebGLRenderer
const webgl = new WebGLApp({
  canvas,
  alpha: false,
  background: '#000',
  backgroundAlpha: 1,
  postprocessing: true,
  showFps: true,
});

// attach webgl to the window to inspect in the console
if (window.DEBUG) {
  window.webgl = webgl;
  window.gui = new GUI();
}

// hide canvas (enabled in asset load callback)
webgl.canvas.style.visibility = 'hidden';

//
// SCENE
//

assets.load({ renderer: webgl.renderer }).then(() => {
  console.log('load callback');

  // show canvas
  webgl.canvas.style.visibility = '';

  // RENDERER CONFIG -- probably move
  // webgl.renderer.physicallyCorrectLights = true;
  // webgl.renderer.toneMapping = THREE.ACESFilmicToneMapping;
  // webgl.renderer.toneMappingExposure

  // ENVIRONMENT & LIGHTING
  addEnvironment(webgl);

  const ocean = new Ocean(); // TODO: add sun direction & fog
  ocean.position.y = 0;
  webgl.scene.add(ocean);

  // ADD MAIN GALLERY SPACE
  const gallery = new Gallery(webgl);
  webgl.scene.gallery = gallery;
  webgl.scene.add(gallery);
  gallery.setup();

  // POSTPROCESSING
  /*
  const saoPass = new SAOPass(webgl.scene, webgl.camera, false, true);
  saoPass.params.saoIntensity = 0.03;
  saoPass.params.saoScale = 100;
  saoPass.renderToScreen = true;
  webgl.composer.addPass(saoPass);
  */

  /*
  if (window.DEBUG) {
    const folder = window.gui.addFolder('SAO Pass');
    gui.add(saoPass.params, 'saoBias', -1, 1);
    gui.add(saoPass.params, 'saoIntensity', 0, 1);
    gui.add(saoPass.params, 'saoScale', 0, 100);
    gui.add(saoPass.params, 'saoKernelRadius', 1, 100);
    gui.add(saoPass.params, 'saoMinResolution', 0, 1);
    gui.add(saoPass.params, 'saoBlur');
    gui.add(saoPass.params, 'saoBlurRadius', 0, 200);
    gui.add(saoPass.params, 'saoBlurStdDev', 0.5, 150);
    gui.add(saoPass.params, 'saoBlurDepthCutoff', 0.0, 0.1);
  }
  */

  // BLOOM
  const bloomPass = new UnrealBloomPass(
    new THREE.Vector2(window.innerWidth, window.innerHeight),
    0.4, // strength 0.3
    0, // radius 0
    0.95 // threshold 0.9
  );
  bloomPass.renderToScreen = true;
  webgl.bloomPass = bloomPass;
  webgl.composer.addPass(bloomPass);

  if (window.DEBUG) {
    const folder = window.gui.addFolder('BloomPass');
    folder.add(bloomPass, 'threshold', 0.0, 3.0).step(0.01);
    folder.add(bloomPass, 'strength', 0.0, 3.0).step(0.01);
    folder.add(bloomPass, 'radius', 0.0, 1.0).step(0.01);
  }

  /* VIGNETTE
  const vignettePass = new ShaderPass({
    vertexShader: passVert,
    fragmentShader: vignetteFrag,
    uniforms: {
      tDiffuse: { type: 't', value: new THREE.Texture() },
    },
  });
  vignettePass.renderToScreen = true;
  webgl.composer.addPass(vignettePass);
  */

  // UI
  // // add the save screenshot button
  // if (window.DEBUG) {
  //   addScreenshotButton(webgl)
  // }

  // ANIMATION
  webgl.start();
  webgl.draw();
});
