import * as THREE from 'three';
import assets from '../lib/AssetManager';
import { HdriPink } from './Materials';

// natural hemisphere light from
// https://threejs.org/examples/#webgl_lights_hemisphere
export function addNaturalLight(webgl) {
  const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.6);
  hemiLight.color.setHSL(0.6, 1, 0.6);
  hemiLight.groundColor.setHSL(0.095, 1, 0.75);
  hemiLight.position.set(0, 50, 0);
  webgl.scene.add(hemiLight);

  const dirLight = new THREE.DirectionalLight(0xffffff, 1);
  dirLight.color.setHSL(0.1, 1, 0.95);
  dirLight.position.set(3, 5, 1);
  dirLight.position.multiplyScalar(50);
  webgl.scene.add(dirLight);
}

export function addEnvironment(webgl) {
  webgl.scene.background = HdriPink;
  webgl.scene.environment = HdriPink;

  // webgl.scene.fog = new THREE.Fog(0xcbaecd, 1000, 1500);

  // ENVIRONMENTAL LIGHTING
  const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.3);
  webgl.scene.hemiLight = hemiLight;
  webgl.scene.add(hemiLight);

  const lightDirKey = new THREE.DirectionalLight(0xffffff, 1);
  lightDirKey.position.set(6, 10, 9);
  lightDirKey.position.multiplyScalar(50);
  webgl.scene.lightDirKey = lightDirKey;
  webgl.scene.add(lightDirKey);

  // const lightDirFill = new THREE.DirectionalLight(0xc9d9ff, 0.5);
  // lightDirFill.position.set(-6, 10, -9);
  // webgl.scene.add(lightDirFill);
}
